/*
 * Polyfill for adding CustomEvent
 * see : https://developer.mozilla.org/fr/docs/Web/API/CustomEvent
 */
if (!window.CustomEvent) { // Create only if it doesn't exist
    (function () {
        function CustomEvent ( event, params ) {
            params = params || { bubbles: false, cancelable: false, detail: undefined };
            var evt = document.createEvent( 'CustomEvent' );
            evt.initCustomEvent( event, params.bubbles, params.cancelable, params.detail );
            return evt;
        };

        CustomEvent.prototype = window.Event.prototype;

        window.CustomEvent = CustomEvent;
    })();
}

function getTargetForComponentReplacement(target, props, options) {
    const container = target.parentElement;
    const targetProps = {
        name: target.name,
        id: target.id,
        ...props
    };
    // console.log("targetProps: ", targetProps);
    target.remove();
    return (!!options) ? (
            Object.assign( {}, {
            target: container,
            props: targetProps,
            ...options
        })
    ) : (
        Object.assign( {}, {
            target: container,
            props: targetProps
        })
    );
}

const shiny_wake_time = 59999;

const appsId = "apps";
const appsElement = document.getElementById(appsId);
export default // Check if element with id exists in DOM
appsElement !== null &&
(appsElement.constructor.name === "HTMLElement" ||
  appsElement.constructor.name === "HTMLBodyElement" ||
  appsElement.constructor.name === "HTMLDivElement"
) ?
  new Apps({
      target: appsElement,
      props: {
        attributes: appsElement.attributes,
        innerElms: (function () {
          const innerElements = document.createElement("div");
          console.log("Apps pre-mount:", appsElement.innerHTML);
          for (const elm of appsElement.childNodes) {
            // console.log(elm.nodeType, elm.innerHTML);
            if (elm.nodeType === 1) {
              innerElements.appendChild(elm);
            }
          }
          appsElement.innerHTML = "";
          return innerElements;
        })(),
        toggle_sidebar_label: "Toggle Developer Sidebar",
      },
      hydrate: true,
    })
  : {};


const headerAppId1 = "cori-header"
const headerAppQuery1 = [
    ".cori .header",
    ".cori * .header",
    ".cori .navbar.navbar-default.navbar-static-top",
    ".cori * .navbar.navbar-default.navbar-static-top"
];
const headerAppElement1 = (document.getElementById(headerAppId1) !== null) ?
    (function () {
        const body = (this || window).document.body;
        // console.log(body);

        const header = (this || window).document.getElementById(headerAppId1);
        // console.log(header);

        body.style.maxHeight = "348px";
        body.style.paddingTop = header.offsetHeight + "px";
        header.style.top = "-" + body.style.paddingTop + "px";

        return header;
    })() :
    (headerAppQuery1.filter(query =>
        (document.querySelectorAll(query) !== null && document.querySelectorAll(query).length > 0)
    ).length > 0)?
        headerAppQuery1.filter(query =>
            (document.querySelectorAll(query) !== null && document.querySelectorAll(query).length > 0)
        ).map(query => (function () {
            const header = (this || window).document.querySelectorAll(query)[0];
            console.log(header);

            return header;
        })())[0] :
            null;
export const headerApp1 =
    (headerAppElement1 !== null && (
        headerAppElement1.constructor.name === "HTMLElement" ||
        headerAppElement1.constructor.name === "HTMLDivElement"
        )
    ) ?
        new CORIHeader({
            target: headerAppElement1,
            props: {
                attributes: headerAppElement1.attributes,
                title: (function () {
                    const attributes = headerAppElement1.attributes;
                    return (attributes !== null && 'title' in attributes) ?
                        attributes['title'].value :
                        ('getElementsByClassName' in headerAppElement1 &&
                            typeof headerAppElement1.getElementsByClassName === 'function' &&
                            headerAppElement1.getElementsByClassName("container-fluid").length > 0) ?
                            ('getElementsByClassName' in headerAppElement1.getElementsByClassName("container-fluid")[0] &&
                                typeof headerAppElement1.getElementsByClassName("container-fluid")[0].getElementsByClassName === 'function' &&
                                headerAppElement1.getElementsByClassName("container-fluid")[0].getElementsByClassName("navbar-header").length > 0) ?
                                headerAppElement1.getElementsByClassName("container-fluid")[0]
                                    .getElementsByClassName("navbar-header")[0]
                                    .getElementsByClassName("navbar-brand")[0].innerText :
                                "" :
                                "";
                })(),
                innerElms: (function () {
                    const innerElements = document.createElement("div");
                    console.log("CORIHeader pre-mount:", headerAppElement1.innerHTML);
                    for (const elm of headerAppElement1.childNodes) {
                        // console.log(elm.nodeType, elm.innerHTML);
                        if (elm.nodeType === 1) {
                            innerElements.appendChild(elm);
                        }
                    }
                    // console.log(innerElements.innerHTML);
                    headerAppElement1.innerHTML = "";
                    return innerElements;
                })(),
                light_on_dark: (function () {
                    const attributes = headerAppElement1.attributes;
                    return (attributes !== null && 'light_on_dark' in attributes) ?
                        (!!attributes['light_on_dark'] && attributes['light_on_dark'].value !== 'false') :
                        (headerAppElement1.getElementsByClassName("container-fluid") !== null);
                })()
            },
            hydrate: true,
        })
        : {};


