const dataToggleButtonAttr = '[data-toggle]'; // ="collapse"
const dataToggleButtonElements = document.querySelectorAll(dataToggleButtonAttr);
export const dataToggleButtonApp =
    dataToggleButtonElements !== null &&
    dataToggleButtonElements.length > 0 &&
    (dataToggleButtonElements[0].constructor.name.toString().match(/HTML\w*Element/) !== null) ?
        (function () {
            // console.log(dataToggleButtonElements);
            const dataToggleButtons = [];
            // const dataToggleButtonElement = dataToggleButtonElements[0];
            for (const dataToggleButtonElement of dataToggleButtonElements) {
                const options = {
                    hydrate: true
                };
                const props = {};
                props["innerElms"] = (function () {
                    const innerElements = document.createElement("div");
                    console.log(
                        "DataToggleButton pre-mount:",
                        dataToggleButtonElement
                    );
                    for (const elm of dataToggleButtonElement.childNodes) {
                        // console.log(elm.nodeType, elm);
                        if (elm.nodeType === 1) {
                            innerElements.appendChild(elm);
                        } else if (elm.nodeType === 3 && "data" in elm) {
                            // console.log(elm.data.toString());
                            props["button_label"] = elm.data;

                            // This does not transpile in certain JS environments (i.e. React)
                            // dataToggleButtonElement.attributes.forEach((attr) => {
                            for (let a in dataToggleButtonElement.attributes) {
                                if (+a === +a) {
                                    console.log(dataToggleButtonElement.attributes[a]);
                                    const attr = dataToggleButtonElement.attributes[a];
                                    console.log(attr.name);
                                    switch (attr.name) {
                                        case "class":
                                            props["className"] = attr.value;
                                            break;
                                        case "aria-expanded":
                                            props["ariaExpanded"] = attr.value;
                                            break;
                                        case "aria-controls":
                                            props["ariaControls"] = attr.value;
                                            break;
                                        case "data-target":
                                            props["dataTarget"] = attr.value;
                                            break;
                                        case "data-toggle":
                                            props["dataToggle"] = attr.value;
                                            break;
                                        default:
                                            "";
                                    }
                                }
                            }
                            //     return true;
                            // });


                        }
                    }
                    dataToggleButtonElement.innerHTML = "";
                    return innerElements;
                })();
                dataToggleButtons.push(
            /*return*/ new DataToggleButton(
                        getTargetForComponentReplacement(
                            dataToggleButtonElement,
                            props,
                            options
                        )
                    )
                );
            }

            return dataToggleButtons;
        })()
        : [];


(function () {
    // console.log(dataToggleButtonElements);

    // Wait a ~ sec to start wake timer
    if (typeof Shiny === 'object') setTimeout(() => {
        console.log("Send input to shiny server every ~ " +
            Math.round(shiny_wake_time / 1000) +
            " seconds to let it know the client is still active");
        setInterval(() => {
            // if (!shiny_module_handler_set) {
            setInput("wake", Math.random());
            // }
        }, shiny_wake_time);
    }, 1533)
    else console.log("Shiny API is not available");
})();
